<template>
  <div class="barCharts-style">
    <div :id="id" class="charts-main"></div>
    <div style="text-align: center">
      <el-pagination
        v-if="ifPage"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="pages.pageNo"
        :page-size="pages.pageSize"
        layout="prev, pager, next, jumper"
        :total="pages.total"
      ></el-pagination>
    </div>
    <showDetailList
      v-if="showDetailVisible"
      :visible.sync="showDetailVisible"
      :detailData="detailData"
      :isQuery="isQuery"
      :isExport="isExport"
      @changePageData="changePageData"
      @exportExcel="exportExcel"
    ></showDetailList>
  </div>
</template>

<script>
/**
 * @柱状图
 * @author：risheng.huang
 * @ 参数说明：
 * barChartsData：{
 *     xNameData:['xxxx','22222'],    //x轴对应的数据值
 *     dataMap:{
 *      '抢修类'：[11,1232,111],
 *      '非抢修类'：[11,1232,111],
 *     }，   // 指标名为key,数据值为数组
 *     colorMap:{       // 颜色值数据
 *        '非抢修类':[['#31fff1','#27ccc1','#808080'],['#0088d1','#025b95','#808080']],
 *         //第一个颜色为渐变颜色，第二个为渐变颜色,第三个颜色为阴影颜色(isMultiple为true时需要)
 *     }
 *   xyName: {'x轴':{ name: '日期', color: "#808080", },'y轴':[ { name: '坐席数', color: '#808080' }]}  //第一个颜色x轴，第二个为y轴
 * }  必须
 * barType：stack/bar 为柱状图的模式，stack是堆叠模式，bar为普通模式
 * barWidth：柱状图的宽度。
 * legendStatus：图例信息是否展示true/false, 默认false
 * yAxiosDw : y轴的单位信息，例“%”
 * changeAxios：x,y轴是否交换 true/false, 默认false
 * percentRange :数值展示取值范围,默认为0~100%,不设置最大值
 * isMultiple: 是否展示多个柱状图,默认false
 * isShadow: 是否展示阴影,默认false
 * name:柱状图名称
 * maxShowNumber:柱状图最大显示个数,超过显示滚动条
 * */
import echarts from "echarts";

import showDetailList from "../show-detail-list/show-detail-list";

export default {
  components: {
    showDetailList
  },

  name: "bar-charts",
  props: {
    barChartsData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    barType: {
      type: String,
      default: () => {
        return "stack";
      }
    },
    ifPage: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    barWidth: {
      type: Number,
      default: () => {
        return 20;
      }
    },
    customizedStyle: {
      type: Number,
      default: () => {
        return 1;
      }
    },
    legendStatus: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    legendPosition: {
      type: String,
      default: () => {
        return "left";
      }
    },
    yAxiosDw: {
      type: String,
      default: () => {
        return "";
      }
    },
    changeAxios: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    percentRange: {
      type: Array,
      default: () => {
        return [];
      }
    },
    fontSizeShow: {
      type: Number,
      default: () => {
        return 11;
      }
    },
    isMultiple: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    isShadow: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    isLabel: {
      type: Boolean,
      default: () => {
        return true;
      }
    },
    showDetail: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    showDetailData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    name: {
      type: String,
      default: () => {
        return "";
      }
    },
    maxShowNumber: {
      type: Number,
      default: () => {
        return 10;
      }
    },
    rotateNum: {
      type: Number,
      default: () => {
        return 28;
      }
    }
  },
  data() {
    return {
      pages: {
        pageNo: 1,
        pageSize: 10,
        total: 0
      },
      id: Math.random() + "_",
      legend: [],
      dataDetails: [],
      xData: [],
      series: [],
      xAxis: {},
      xyName: {},
      fontSize: this.fontSizeShow,
      showDetailVisible: false,
      detailData: {},
      isQuery: false,
      isExport: true
    };
  },
  watch: {
    showDetailData() {
      window.console.log(this.showDetailData, "showDetailData");

      this.detailData = {};
      this.$nextTick(() => {
        if (Object.keys(this.showDetailData).length !== 0) {
          this.detailData = { ...this.showDetailData };
          this.showDetailVisible = true;
          this.isQuery = this.showDetailData.isQuery
            ? this.showDetailData.isQuery
            : false;
          this.isExport = this.showDetailData.isExport;
        }
      });
    }
  },
  created() {},
  mounted() {
    this.xData = this.barChartsData.xNameData
      ? this.barChartsData.xNameData
      : [];
    this.xyName = this.barChartsData.xyName;
    this.filterData(
      this.barType,
      this.barChartsData.dataMap,
      this.barChartsData.colorMap
    );
    this.$nextTick(() => {
      this.init();
    });
  },
  methods: {
    handleSizeChange(val) {
      this.pages.pageSize = val;
      this.$parent.changeSize(this.pages, this.name);
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pages.pageNo = val;
      this.$parent.changePageNo(this.pages, this.name);
      console.log(`当前页: ${val}`);
    },
    filterData(type, data, colorMap) {
      let series = [];
      let count = -1;
      let dw = this.yAxiosDw;
      let that = this;
      if (data) {
        for (let key in data) {
          this.dataDetails.push(data[key]);
          this.legend.push(key);
          let colorGl = colorMap[key];
          count++;
          //处理颜色
          let colorGlDeal = [...colorMap[key]];
          let dataLength = this.dataDetails[count].length;
          let colorLength = colorGlDeal.length;
          if (dataLength > colorLength) {
            for (let i = 0; i < dataLength - colorLength; i++) {
              if (colorLength === 1) {
                //单色模式
                colorGl.push(JSON.parse(JSON.stringify(colorGlDeal[0])));
              } else {
                colorGl.push(
                  JSON.parse(
                    JSON.stringify(
                      colorGlDeal[i - colorLength * Math.floor(i / colorLength)]
                    )
                  )
                );
              }
            }
          }
          let _series = {
            type: "bar",
            name: key,
            barMaxWidth: this.barWidth,
            stack: this.barType === "stack" ? "stack" : "",
            yAxisIndex: count,
            showBackground: true,
            backgroundStyle: this.isShadow
              ? {
                  color: colorGl[0][2]
                }
              : {
                  color: "transparent"
                  // borderColor: function (params, index) {
                  //   let colorList = []
                  //   colorGl.forEach(e => {
                  //     colorList.push(e[0])
                  //   })
                  //   console.log(colorList, 'colorList');
                  //   return colorList[index];
                  // },
                  // borderWidth: 2
                },
            label: this.isMultiple
              ? {
                  show: this.isLabel,
                  position: "top",
                  formatter: function(params) {
                    return params.value;
                  },
                  color: colorGl[0][0]
                }
              : {
                  show: this.isLabel,
                  position: "top",
                  formatter: function(params) {
                    if (that.isShadow) {
                      return params.value + dw;
                    } else {
                      return params.value > 0 ? params.value + dw : "";
                    }
                  },
                  color: function(params, index) {
                    let colorList = [];
                    colorGl.forEach(e => {
                      colorList.push(e[0]);
                    });
                    return colorList[index];
                  }
                },
            itemStyle: {
              normal:
                this.isMultiple || this.isShadow
                  ? {
                      color: {
                        type: "linear",
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [
                          {
                            offset: 0,
                            color: colorGl[0][0] // 0% 处的颜色
                          },
                          {
                            offset: 1,
                            color: colorGl[0][1] // 100% 处的颜色
                          }
                        ],
                        global: false // 缺省为 false
                      }
                    }
                  : {
                      color: function(params) {
                        var colorItem = colorGl[params.dataIndex];
                        return new echarts.graphic.LinearGradient(
                          0,
                          0,
                          0,
                          1,
                          [
                            {
                              offset: 0,
                              color: colorItem[0]
                            },
                            {
                              offset: 1,
                              color: colorItem[1]
                            }
                          ],
                          false
                        );
                      }
                    }
            },
            data: this.dataDetails[count]
          };
          //x轴
          this.xAxis = {
            data: this.xData,
            name: this.xyName["x轴"].name,
            nameTextStyle: {
              verticalAlign: "top",
              padding: [8, 0, 0, 0],
              color: this.xyName["x轴"].color
            },
            axisLabel: {
              interval: 0,
              show: true,
              rotate: this.rotateNum,
              fontSize: this.fontSize,
              formatter: function(params) {
                // var val = "";
                // if (params.length > 7) {
                //   val = params.substr(0, 7) + '..';
                //   return val;
                // }else {
                //   return  params
                // }
                var val = "";
                if (params.length > 7 && params.length <= 10) {
                  val = params.substr(0, 7) + "\n" + params.substr(7, 3);
                  return val;
                } else if (params.length > 10) {
                  val = params.substr(0, 6) + "\n" + params.substr(6, 6) + "..";
                  return val;
                } else {
                  return params;
                }
              },
              textStyle: {
                fontSize: this.fontSize,
                color: this.isMultiple
                  ? {
                      color: "colorGl[0][0]"
                    }
                  : this.xyName["x轴"].diffcolor
                  ? function(params, index) {
                      let colorList = [];
                      colorGl.forEach(e => {
                        colorList.push(e[0]);
                      });
                      return colorList[index];
                    }
                  : this.xyName["x轴"].color
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: "#000"
              }
            },
            axisLine: {
              lineStyle: {
                color: "#d9f0f2"
              }
            }
          };
          //y轴
          this.yAxis = [];
          this.xyName["y轴"].forEach((e, i) => {
            this.yAxis.push({
              type: "value",
              name: e.name,
              nameGap: 30,
              nameTextStyle: {
                align: e.name.length > 5 ? "center" : "right",
                color: e.color
              },
              // max: this.percentRange[0],
              // min: this.percentRange[1],
              splitLine: {
                show: i === 1 ? false : true,
                lineStyle: {
                  color: "#d9f0f2"
                }
              },
              axisLine: {
                show: false,
                lineStyle: {
                  color: "#d9f0f2"
                }
              },
              axisLabel: {
                show: true,
                fontSize: this.fontSize,
                formatter: function(value) {
                  return value + dw;
                },
                color: e.color,
                interval: 0,
                rotate: 0
              }
            });
          });
          if (this.xyName["y轴"].length === 1) {
            _series.yAxisIndex = 0;
          }
          series.push(_series);
        }
      }
      this.series = series;
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let xData = this.xData;
      let dw = this.yAxiosDw;
      let options = {
        dataZoom:
          xData.length > this.maxShowNumber
            ? [
                {
                  type: "slider", // 内置于坐标系中
                  startValue: 0,
                  endValue: this.maxShowNumber,
                  xAxisIndex: [0],
                  height: 22,
                  showDetail: false,
                  showDataShadow: false,
                  fillerColor: "rgba(0,0,0,0.2)"
                }
              ]
            : [],
        tooltip: {
          formatter: function(a) {
            return a.name + "<br>" + a.seriesName + "：" + a.value + dw;
          }
        },
        grid: {
          left: 60,
          right: 60,
          bottom: 56
        },
        legend: {
          show: this.legendStatus,
          data: this.legend,
          itemWidth: 12,
          itemHeight: 12,
          textStyle: {
            color: "#808080",
            // fontFamily: 'Noto Sans SC',
            fontSize: this.fontSize
          },
          top: 10
        },
        xAxis: this.xAxis,
        yAxis: this.yAxis,
        series: this.series
      };
      if (this.legendPosition === "right") {
        options.legend.right = 50;
      }
      if (this.legendPosition === "left") {
        options.legend.left = 100;
      }
      if (this.changeAxios) {
        options.xAxis = options.yAxis;
        options.yAxis = options.xAxis;
        let xLen = 0;
        if (xData) {
          for (let v of xData) {
            if (v.length >= xLen) {
              xLen = v.length;
            }
          }
        }
        if (xLen >= 4) {
          options.grid.left = 80;
        } else {
          options.grid.left = 50;
        }
        if (this.legendStatus === false) {
          options.grid.top = 10;
        }
      }
      charts.setOption(options);
      this.$emit("setOption", this.name, charts);
      charts.on("click", params => {
        // if (this.showDetail) {
        this.$emit("clickBar", params);
        // }
      });
    },
    //导出
    exportExcel(data) {
      this.$emit("exportExcel", data);
    },
    //点击详情切换分页
    changePageData(data) {
      this.$emit("changePageData", data);
    }
  }
};
</script>
<style scoped lang="scss">
.barCharts-style {
  position: relative;
  width: 100%;
  height: 100%;
  .charts-main {
    width: 100%;
    height: calc(100% - 30px);
  }
}
</style>